












import Vue from 'vue'
export default Vue.extend({
  props: {
    title: {
      type: String
    },
    showBack: {
      type: Boolean,
      default: false
    },
    showTitle: {
      type: Boolean,
      default: true
    }
  },
  methods: {
    onBack() {
      this.$emit('back')
    }
  }
})
