






















import Vue from 'vue'
import Header from '../components/Header.component.vue'
export default Vue.extend({
  data: function() {
    return {
      articleUrl:
        'https://www.oschina.net/news/106799/the-struggles-of-an-open-source-maintainer'
    }
  },
  components: {
    Header
  },
  methods: {
    onBack() {
      this.$router.push({
        name: 'home'
      })
    },
    onClick() {
      this.$router.push({
        name: 'convert-article',
        params: {
          articleUrl: this.articleUrl
        }
      })
    }
  }
})
